.content-spinner {
  display: inline-block;
  margin: auto;
}

.spinner {
  height: 38px;
  width: 38px;
  animation: rotate 0.6s infinite linear;
  border: 3px solid #9C9C9C;
  border-right-color: transparent;
  border-radius: 50%;
}

@keyframes rotate {
  0%    { transform: rotate(0deg); }
  100%  { transform: rotate(360deg); }
}

p.loading-p {
  text-align: center;
  z-index: 0;
  position: absolute;
  left: 0;
  right: 0;
  top: 60px;
}
