$margin: 0px;

.ember-modal-overlay {
  height: 100vh;
  left: 0;
  position: fixed;
  right: 0;
  top: 0;
  z-index: 50;
  background-color: rgba(0, 0, 0, 0.31);
}


.ember-modal-dialog {
  border-radius: 0px;
  background-color: #fff;
  box-shadow: 0 0 10px #222;
  padding: 20px;

  // Aumenta el tama√±o de la ventana

  top: $margin !important;
  bottom: $margin !important;
  left: $margin !important;
  right: $margin !important;

  margin-left: 0px !important;
  margin-top: 0px !important;
}

.modal-footer {
  bottom: 10px;
  position: absolute;
  left: 10px;
  right: 10px;
}


.about-dialog {
  margin-top: 20px !important;
  margin-left: 20px !important;
  margin-right: 20px !important;
  margin-bottom: 20px !important;
}

.settings-dialog {
  margin-top: 20px !important;
  margin-left: 20px !important;
  margin-right: 20px !important;
  height: 270px;
}


.setup-dialog {
  margin-top: 20px !important;
  margin-left: 20px !important;
  margin-right: 20px !important;
  height: 270px;
}
